import React, { useState }  from "react";
import { makeStyles }       from "@material-ui/styles";
import { Typography }       from "@material-ui/core";
import * as Utils           from "helpers/utils";
import badge                from "assets/img/badge.png";
import badgeShield          from "assets/img/shield.png";

const Badge = ({ text, size, shield, className, style, userID }) => {
    const [ showName, setShowName ] = useState(false);
    const classes = useStyles();
    const styles = { width: size, height: size };
    const goToProfile = () => Utils.openModal("ViewProfile", userID);

    if (shield)
        return <img src={ badgeShield } className={ className } style={{ ...styles, ...style }} alt="Badge Shield"/>

    return (
        <>
            {(showName && text) && (
                <button className={ classes.userButton } onClick={ goToProfile }>
                    <Typography className={ classes.name } variant="overline">{ text }</Typography>
                </button>
            )}
            <button onClick={ () => setShowName(!showName) } className={ classes.button }>
                <img src={ badge } style={ styles } className={ className } alt="Badge"/>
            </button>
        </>
    )
}

export default Badge;

Badge.defaultProps = {
    size: 60
}

const useStyles = makeStyles(theme => ({
    button: {
        background: "transparent",
        border: "none",
    },
    name: {
        color: "#FFF",
    },
    userButton: {
        border: "none",
        background: "rgba(0,0,0,0.6)",
        borderRadius: 3,
        padding: 2,
        position: "absolute",
        top: -30,
    }
}));
