import * as general from "actions/general";
import { store }    from "reducers/store";

export const isDev = window.location.href.search("localhost") > -1 && window.location.href.search("3000") > -1

export function handleLink(link) {
    if (!link || link.length === 0)
        return null;

    if (link.search("@") > -1)
        link = "mailto:" + link;
    else if (link.search("http") === -1)
        link = "tel:" + link;

    return link;
}

export function limitText(text, limit = 30) {
    if (text.length < limit)
        return text;

    return `${text.slice(0, limit)}...`;
}

export function openModal(modal, data) {
    return store.dispatch( general.openModal(modal, data) );
}

export function closeModal(modal, data) {
    return store.dispatch( general.closeModal() );
}

export function notificationSuccess(message) {
    return store.dispatch( general.updateAlert(true, "success", message) );
}

export function notificationError(message) {
    return store.dispatch( general.updateAlert(true, "error", message) );
}

export function getRegionWithDelta(points) {
    // points should be an array of { latitude: X, longitude: Y }
    let minX, maxX, minY, maxY;

    // init first point
    ((point) => {
        minX = point.latitude;
        maxX = point.latitude;
        minY = point.longitude;
        maxY = point.longitude;
    })(points[0]);

    // calculate rect
    points.map((point) => {
        minX = Math.min(minX, point.latitude);
        maxX = Math.max(maxX, point.latitude);
        minY = Math.min(minY, point.longitude);
        maxY = Math.max(maxY, point.longitude);
        return null;
    });

    const midX = (minX + maxX) / 2;
    const midY = (minY + maxY) / 2;
    const deltaX = (maxX - minX);
    const deltaY = (maxY - minY);

    return {
        latitude: midX,
        longitude: midY,
        latitudeDelta: deltaX,
        longitudeDelta: deltaY
    }
}

export function getAddress(address, showState = false) {
    const { street, number, neighborhood, city, state, country } = address;

    if (showState)
        return `${ state }, ${ country }`;

    return `${street}, ${ number } - ${ neighborhood }, ${ city }`;
}

export function objectToURL(obj) {
    return Object.entries(obj).map(([key, val]) => `${key}=${val}`).join('&');
}

export function getTime(d = new Date(), addSecs = false) {
    if (typeof d !== "object")
        d = new Date(d);

    const hour = (d.getHours() < 10) ? "0"+d.getHours() : d.getHours();
    const mins = (d.getMinutes() < 10) ? "0"+d.getMinutes() : d.getMinutes();
    let response = `${hour}:${mins}`;

    if (addSecs)
        response += (d.getSeconds() < 10) ? "0"+d.getSeconds() : d.getSeconds();

    return response;
}

export function getDate(d = new Date()) {
    if (typeof d !== "object")
        d = new Date(d);

    const year = d.getFullYear();
    const month = ("0" + (d.getMonth() + 1)).slice(-2);
    const date = ("0" + d.getDate()).slice(-2);

    return `${date}/${month}/${year}`;
}

export function calculateAge(birthday) {
    if (typeof birthday === "string") {
        const parts = birthday.split("/");
        birthday = new Date(parts[2], parseInt(parts[1]) - 1, parts[0]);
    }

    const ageDifMs = Date.now() - birthday.getTime();
    const ageDate = new Date(ageDifMs);

    return Math.abs(ageDate.getUTCFullYear() - 1970);
}

export function clearString(text, extra = []) {
    let newText = "";
    let clearStringFromThese = [".", ",", "/", "\\", "|", "-", "_", "(", ")", " ", ...extra];

    for (var i = 0; i < text.length; i++)
        if (!clearStringFromThese.includes(text[i]))
            newText += text[i];

    return newText;
}

export function mask(text, mask) {
    let newText = "";
    text = clearString(text);

    let j = 0;
    for (var i = 0; i < text.length; i++) {
        if (i <= mask.length - 1) {
            if (mask[j] !== "#") {
                newText += mask[j]; j++;
                newText += text[i];
            } else {
                newText += text[i];
            }
            j++;
        }
    }

    return newText.slice(0, mask.length);
}

export function validateEmail(email) {
    // eslint-disable-next-line
    let re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return re.test(String(email).toLowerCase());
}
