import React, { useState, useEffect }  from "react";
import { connect }          from "react-redux";
import { getHelps }         from "actions/help";
import { makeStyles }       from "@material-ui/styles";
import { Grid }             from "@material-ui/core";
import * as Utils           from "helpers/utils";
import { Loader, HelpCard } from "components";

const HelpList = ({ getHelps, filter }) => {
    const classes = useStyles();
    const [ helps, setHelps ] = useState([]);
    const [ loader, setLoader ] = useState(true);

    useEffect(() => {
        getHelps(filter, res => {
            setLoader(false);

            if (res === false)
                return Utils.notificationError("Não foi possível buscar os dados no momento");

            return setHelps(res);
        });
    }, [getHelps, filter]);

    return (
        <div className={ classes.root }>
            <Loader active={ loader }/>
            {(!loader) && (
                <div className={ classes.content }>
                    <Grid container spacing={ 3 }>
                        { helps.map(help =>
                            <Grid item key={ help._id } lg={ 4 } md={ 6 } xs={ 12 }>
                                <HelpCard help={ help } />
                            </Grid>
                        )}
                    </Grid>
                </div>
            )}
        </div>
    )
}

export default connect(null, { getHelps })(HelpList);

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(3),
        height: "90%"
    },
    content: {
        marginTop: theme.spacing(2)
    },
    pagination: {
        marginTop: theme.spacing(3),
        display: "flex",
        alignItems: "center",
        justifyContent: "flex-end"
    },
    row: {
        height: "42px",
        display: "flex",
        alignItems: "center",
        marginTop: theme.spacing(1)
    },
}));
