import React, { useState, useEffect }     from "react";
import { Link as RouterLink, withRouter } from "react-router-dom";
import { connect }          from "react-redux";
import { makeStyles }       from "@material-ui/styles";
import { login }            from "actions/user";
import * as Utils           from "helpers/utils";
import ArrowBackIcon        from "@material-ui/icons/ArrowBack";
import {
    FormHelperText,
    Typography,
    IconButton,
    TextField,
    Button,
    Grid,
    Link,
} from "@material-ui/core";
// import { Facebook as FacebookIcon, Google as GoogleIcon } from "assets/icons";

const Login = ({ history, loginAction }) => {
    const classes = useStyles();
    const [ login, setLogin ] = useState("");
    const [ password, setPassword ] = useState("");
    const [ error, setError ] = useState(null);
    const onChange = ({ target: { value, name }}) => {
        if (name === "login")
            return setLogin(value);

        return setPassword(value);
    }

    const handleLogin = event => {
        event.preventDefault();

        if (login.length < 5)
            return setError({ e: "login", m: "Preencha o login corretamente" });

        if (login.length > 0 && Utils.validateEmail(login) === false)
            return setError({ e: "login", m: "Informe um email válido" });

        if (password.length < 5)
            return setError({ e: "password", m: "Preencha a senha corretamente" });

        return loginAction(login, password, status => {
            if (status)
                return history.push("/home");

            return setError({ e: "request", m: "Não foi possível fazer o login. Verifique seus dados e tente novamente." })
        });
    }

    useEffect(() => {
        if (login.length > 0 && Utils.validateEmail(login) === false) {
            setError({ e: "login", m: "Informe um email válido" });
        } else {
            setError(null);
        }
    }, [login]);

    useEffect(() => setError(null), [password]);

    return (
        <div className={ classes.root }>
            <Grid className={ classes.grid } container>
                <Grid className={ classes.quoteContainer } item lg={ 5 }>
                    <div className={classes.quote}>
                        <div className={classes.quoteInner}>
                            <Typography className={classes.quoteText} variant="h1">Dar as mãos por uma boa causa:<br/>esse é o nosso jogo!</Typography>
                            <Typography className={classes.bio} variant="body2">(No sentido figurado, nada de contato físico)</Typography>
                            <Typography className={classes.name} variant="body1">Autor Desconhecido</Typography>
                        </div>
                    </div>
                </Grid>
                <Grid className={classes.content} item lg={7} xs={12}>
                    <div className={classes.content}>
                        <div className={classes.contentHeader}>
                            <IconButton onClick={ history.goBack }>
                                <ArrowBackIcon />
                            </IconButton>
                        </div>
                        <div className={classes.contentBody}>
                            <form className={classes.form} onSubmit={ handleLogin }>
                                <Typography className={classes.title} variant="h2">
                                    Fazer login
                                </Typography>
                                {/*<Typography
                                  color="textSecondary"
                                  gutterBottom
                                >
                                  Faça login com suas redes sociais
                                </Typography>
                                <Grid
                                  className={classes.socialButtons}
                                  container
                                  spacing={2}
                                >
                                  <Grid item>
                                    <Button
                                      color="primary"
                                      onClick={handleLogin}
                                      size="large"
                                      variant="contained"
                                    >
                                      <FacebookIcon className={classes.socialIcon} />
                                      Facebook
                                    </Button>
                                  </Grid>
                                  <Grid item>
                                    <Button
                                      onClick={handleLogin}
                                      size="large"
                                      variant="contained"
                                    >
                                      <GoogleIcon className={classes.socialIcon} />
                                      Google
                                    </Button>
                                  </Grid>
                                </Grid>
                                <Typography
                                  align="left"
                                  className={classes.sugestion}
                                  color="textSecondary"
                                  variant="body1"
                                >
                                  ou faça login usando seu email
                                </Typography>*/}
                                <TextField
                                  className={ classes.textField }
                                  fullWidth
                                  error={ error && error.e === "login" }
                                  helperText={ (error && error.e === "login") ? error.m : null }
                                  label="Email"
                                  name="login"
                                  onChange={ onChange }
                                  type="text"
                                  value={ login }
                                  variant="outlined"
                                />
                                <TextField
                                    className={classes.textField}
                                    error={ error && error.e === "password" }
                                    fullWidth
                                    helperText={ (error && error.e === "password") ? error.m : null }
                                    label="Senha"
                                    name="password"
                                    onChange={ onChange }
                                    type="password"
                                    value={ password }
                                    variant="outlined"
                                />

                                {(error && error.e === "request") && (
                                    <FormHelperText error>{ error.m }</FormHelperText>
                                )}

                                <Button
                                    className={ classes.signInButton }
                                    color="primary"
                                    disabled={ !!error || !login.length || !password.length }
                                    fullWidth
                                    size="large"
                                    type="submit"
                                    variant="contained"
                                >
                                    Fazer Login
                                </Button>
                                <Typography color="textSecondary" variant="body1">
                                    {"Não possui conta? "}
                                    <Link component={ RouterLink } to="/new-account" variant="h6">Criar conta</Link>
                                </Typography>
                            </form>
                        </div>
                    </div>
                </Grid>
            </Grid>
        </div>
    )
}

const mapStateToProps = state => ({
    user: state.user.user
});

export default connect(mapStateToProps, { loginAction: login })(withRouter(Login));

const useStyles = makeStyles(theme => ({
    root: {
        backgroundColor: theme.palette.background.default,
        height: "100%"
    },
    grid: {
        height: "100%"
    },
    quoteContainer: {
        [theme.breakpoints.down("md")]: {
            display: "none"
        }
    },
    quote: {
        backgroundColor: theme.palette.neutral,
        height: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        backgroundImage: "url(/images/auth.jpg)",
        backgroundSize: "cover",
        backgroundRepeat: "no-repeat",
        backgroundPosition: "center"
    },
    quoteInner: {
        textAlign: "center",
        flexBasis: "600px"
    },
    quoteText: {
        color: theme.palette.white,
        fontWeight: 300
    },
    name: {
        marginTop: theme.spacing(1),
        color: theme.palette.white
    },
    bio: {
        color: theme.palette.white,
        marginTop: 5
    },
    contentContainer: {},
    content: {
        height: "100%",
        display: "flex",
        flexDirection: "column"
    },
    contentHeader: {
        display: "flex",
        alignItems: "center",
        paddingTop: theme.spacing(2),
        paddingBototm: theme.spacing(2),
        paddingLeft: theme.spacing(2),
        paddingRight: theme.spacing(2)
    },
    logoImage: {
        marginLeft: theme.spacing(4)
    },
    contentBody: {
        flexGrow: 1,
        display: "flex",
        alignItems: "center",
        [theme.breakpoints.down("md")]: {
            justifyContent: "center"
        }
    },
    form: {
        paddingLeft: 100,
        paddingRight: 100,
        paddingBottom: 125,
        flexBasis: 700,
        [theme.breakpoints.down("sm")]: {
            paddingLeft: theme.spacing(2),
            paddingRight: theme.spacing(2)
        }
    },
    title: {
        marginTop: theme.spacing(3)
    },
    socialButtons: {
        marginTop: theme.spacing(3)
    },
    socialIcon: {
        marginRight: theme.spacing(1)
    },
    sugestion: {
        marginTop: theme.spacing(2)
    },
    textField: {
        marginTop: theme.spacing(2)
    },
    signInButton: {
        margin: theme.spacing(2, 0)
    }
}));
