import * as c from "helpers/constants";

export function updateAlert(status = false, _type = "success", message = "") {
    return dispatch => dispatch({ type: c.UPDATE_ALERT, status, _type, message });
}

export function closeModal() {
    return dispatch => dispatch({ type: c.CLOSE_MODAL });
}

export function openModal(name, data) {
    return dispatch => dispatch({ type: c.OPEN_MODAL, name, data });
}
