import * as c from "helpers/constants";

let initialState = {
    alert: {
        status: false,
        type: "success",
        message: "",
    },
    modal: {
        show: false,
        name: "",
        data: null,
    }
};

export default function(state = initialState, action) {
    switch (action.type) {
        case c.UPDATE_ALERT: {
            const { status, _type: type, message } = action;

            return {
                ...state,
                alert: {
                    status, type, message
                }
            }
        }
        case c.OPEN_MODAL: {
            const { name, data } = action;

            return {
                ...state,
                modal: {
                    show: true,
                    name,
                    data
                }
            }
        }
        case c.CLOSE_MODAL: {
            return {
                ...state,
                modal: {
                    show: false,
                    name: "",
                    data: null
                }
            }
        }
        case c.LOGOUT: {
            return initialState;
        }
        default:
            return state;
    }
};
