import React                from "react";
import * as Utils           from "helpers/utils";
import { connect }          from "react-redux";
import { makeStyles }       from "@material-ui/styles";
import { Typography }       from "@material-ui/core";
import { ProfilePicture }   from "components";

const useStyles = makeStyles(theme => ({
    root: {
        display: "flex",
        flexDirection: "column",
        alignItems: "center",
        minHeight: "fit-content"
    },
    name: {
        marginTop: theme.spacing(1)
    },
    age: {
        fontSize: 10,
    },
    email: {
        marginTop: 1
    }
}));

const Profile = ({ className, user }) => {
    const classes = useStyles();

    return (
        <div className={`${classes.root} ${className}`}>
            <ProfilePicture size={ 60 }/>
            <Typography variant="h4" className={ classes.name }>{ user.name }</Typography>
            <Typography variant="body2" className={ classes.email }>{ user.email }</Typography>
            <Typography variant="body2" className={ classes.age }>{ Utils.calculateAge(user.birthdate) } anos</Typography>
        </div>
    )
}

const mapStateToProps = state => ({
    user: state.user.user
});

export default connect(mapStateToProps)(Profile);
