// import { TranslationsKeys } from "../translations/Enums";
import * as c               from "../helpers/constants";

let initialState = {
    user: {},
    onboarding: false,
    showVolunteerModal: true,
    language: "en"//TranslationsKeys.PORTUGUESE
};

export default function(state = initialState, action) {
    switch (action.type) {
        case c.SET_USER: {
            return {
                ...state,
                user: {
                    ...state.user,
                    ...action.user,
                }
            }
        }
        case c.UPDATE_LANGUAGE: {
            return {
                ...state,
                language: action.language
            }
        }
        case c.LOGOUT: {
            return initialState;
        }
        default:
            return state;
    }
};
