import React, { useState, useEffect }  from "react";
import { connect }          from "react-redux";
import { searchUsers }      from "actions/user";
import { makeStyles }       from "@material-ui/styles";
import { Grid }             from "@material-ui/core";
import * as Utils           from "helpers/utils";
import {
    SearchInput,
    UserCard,
    Loader
} from "components";

let timer = null;

const SearchVolunteer = ({ searchUsers }) => {
    const classes = useStyles();
    const [ users, setUsers ] = useState([]);
    const [ loader, setLoader ] = useState(false);
    const [ search, setSearch ] = useState("");

    useEffect(() => {
        if (search.length > 0) {
            setLoader(true);

            clearTimeout(timer);

            timer = setTimeout(() => {
                return searchUsers(search, res => {
                    setLoader(false);

                    if (res === false)
                        return Utils.notificationError("Não foi possível exibir o resultado da pesquisa");

                    return setUsers(res);
                });
            }, 700);
        }
    }, [search, searchUsers]);

    return (
        <div className={ classes.root }>
            <div className={classes.row}>
                <SearchInput onChange={ ({ target }) => setSearch(target.value) } placeholder="Buscar voluntário" className={ classes.searchInput }/>
            </div>
            <Loader active={ loader }/>
            {(!loader) && (
                <div className={ classes.content }>
                    <Grid container spacing={ 3 }>
                        { users.map(user =>
                            <Grid item key={ user._id } lg={ 4 } md={ 6 } xs={ 12 }>
                                <UserCard user={ user } />
                            </Grid>
                        )}
                    </Grid>
                </div>
            )}
        </div>
    )
}

export default connect(null, { searchUsers })(SearchVolunteer);

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(3),
        height: "90%"
    },
    content: {
        marginTop: theme.spacing(2)
    },
    pagination: {
        marginTop: theme.spacing(3),
        display: "flex",
        alignItems: "center",
        justifyContent: "flex-end"
    },
    row: {
        height: "42px",
        display: "flex",
        alignItems: "center",
        marginTop: theme.spacing(1)
    },
}));
