import React, { useState, useRef } from "react";
import * as Utils           from "helpers/utils";
import { connect }          from "react-redux";
import { makeStyles }       from "@material-ui/styles";
import { updatePicture }    from "actions/user";
import { ProfilePicture }   from "components";
import {
    Card,
    CardActions,
    CardContent,
    Typography,
    Divider,
    Button,
} from "@material-ui/core";

const AccountProfile = ({ className, user, updatePicture }) => {
    const [ file, setFile ] = useState(null);
    const fileRef = useRef();
    const classes = useStyles();

    const onImageChange = ({ target }) => setFile(target.files[0]);
    const openFile = () => fileRef.current.click();
    const saveData = () => updatePicture(file, status => {
        if (status)
            return Utils.notificationSuccess("Foto atualizada com sucesso!");

        return Utils.notificationError("Não foi possível atualizar sua foto no momento");
    });

    return (
        <Card className={ className }>
            <CardContent>
                <div className={ classes.details }>
                    <div>
                        <Typography gutterBottom variant="h2">{ user.name }</Typography>
                        <Typography className={ classes.locationText } color="textSecondary" variant="body1">
                            { user.address.city }, { user.address.country }
                        </Typography>
                        <Typography className={ classes.dateText } color="textSecondary" variant="body1">
                            Perfil criado { Utils.getDate(user.created) } às { Utils.getTime(user.created) }
                        </Typography>
                    </div>
                    <ProfilePicture source={ file ? URL.createObjectURL(file) : null }/>
                </div>
            </CardContent>
            <Divider />

            <CardActions>
                <input
                    ref={ fileRef }
                    accept="image/*"
                    className={classes.input}
                    multiple
                    type="file"
                    onChange={ onImageChange }
                />
                <Button onClick={ openFile } className={ classes.uploadButton } color="primary" variant="text">Atualizar Foto</Button>
                <Button onClick={ saveData } disabled={ !file } color="primary" variant="contained">Salvar Foto</Button>
            </CardActions>
        </Card>
    )
}

const mapStateToProps = state => ({
    user: state.user.user
});

export default connect(mapStateToProps, { updatePicture })(AccountProfile);

const useStyles = makeStyles(theme => ({
    details: {
        display: "flex"
    },
    progress: {
        marginTop: theme.spacing(2)
    },
    uploadButton: {
        marginRight: theme.spacing(2)
    },
    input: {
        display: "none",
    },
}));
