import React, { useState }  from "react";
import { Redirect }         from "react-router-dom";
import { makeStyles }       from "@material-ui/styles";
import { connect }          from "react-redux";
import { updateUser }       from "actions/user";
import { Loader }           from "components";
import * as Utils           from "helpers/utils";
import volunteerImage       from "assets/img/onboarding/4-seja-voluntario.png";
import {
    CardContent,
    Typography,
    Button,
    Card,
    Grid,
} from "@material-ui/core";

const BecomeVolunteer = ({ updateUser, user, history }) => {
    const [ loader, setLoader ] = useState(false);
    const [ redirect, setRedirect ] = useState(false);
    const classes = useStyles();
    const onButtonPress = () => {
        setLoader(true);

        return updateUser({ volunteer: !user.volunteer }, status => {
            setLoader(false);

            if (!status)
                return Utils.notificationError("Não foi possível executar essa ação. Tente novamente");

            setRedirect(true);
            return Utils.notificationSuccess(user.volunteer ? "Seu perfil de voluntário foi desfeito com sucesso!" : "Parabens! Agora você é um voluntário!");
        })
    }

    if (redirect)
        return <Redirect to="/home"/>

    return (
        <div className={ classes.root }>
            <Grid container spacing={ 4 }>
                <Grid item md={ 5 } xs={ 12 }>
                    <Card>
                        <CardContent>
                            {(user.volunteer) ? (
                                <>
                                    <Text>Se deseja deixar de ser um voluntário, é bem simples.</Text>
                                    <Text>Após clicar no botão abaixo, seu perfil deixará de ficar disponível para os outros usuários.</Text>
                                    <Text>Caso queira desfazer essa ação, é só pressionar novamente o botão.</Text>
                                </>
                            ) : (
                                <>
                                    <Text>Não precisamos de mais nenhuma informação para você se tornar um voluntário.</Text>
                                    <Text>Precismos apenas que você tenha certeza disso.</Text>
                                    <Text>A partir dessa decisão, você poderá ser encontrado por qualquer pessoa que precisar de ajuda.</Text>
                                    <Text>Mas seus dados não serão divulgados até que você autorize.</Text>
                                </>
                            )}

                            <Loader active={ loader } style={{ height: 250 }}/>

                            {(!loader) && (
                                <img src={ volunteerImage } className={ classes.image } alt="Seja um voluntário"/>
                            )}

                            <Button onClick={ onButtonPress } className={ classes.button } color="primary" fullWidth size="large" variant="contained">
                                { user.volunteer ? "Deixar de Ser Voluntário" : "Me Tornar Voluntário" }
                            </Button>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </div>
    )
}

const mapStateToProps = state => ({
    user: state.user.user
});

export default connect(mapStateToProps, { updateUser })(BecomeVolunteer);

const Text = ({ children }) => {
    const classes = useStyles();

    return (
        <Typography className={ classes.text } variant="subtitle2">{ children }</Typography>
    )
}

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(4)
    },
    text: {
        fontSize: 18,
        lineHeight: "25px",
        textAlign: "center",
        marginBottom: 20
    },
    image: {
        width: "50%",
        objectFit: "contain",
        margin: "5% 25% 10%"
    }
}));
