import React            from "react";
import { Link }         from "react-router-dom";
import { connect }      from "react-redux";
import { logout }       from "actions/user";
import { makeStyles }   from "@material-ui/styles";
import MenuIcon         from "@material-ui/icons/Menu";
import InputIcon        from "@material-ui/icons/Input";
import {
    IconButton,
    Toolbar,
    AppBar,
    Hidden,
} from "@material-ui/core";

const Topbar = ({ className, onSidebarOpen, history, logout, minimal, ...rest }) => {
    const classes = useStyles();

    return (
        <AppBar className={ classes.root }>
            <Toolbar>
                <Link to="/home">
                    <img alt="Logo" src="/images/logos/logo-inverse.png" width="180px" style={{ marginTop: 5 }}/>
                </Link>
                {(!minimal) && (
                    <>
                        <div className={ classes.flexGrow } />
                        <Hidden mdDown>
                            <IconButton onClick={ logout } className={ classes.signOutButton } color="inherit">
                                <InputIcon />
                            </IconButton>
                        </Hidden>
                        <Hidden lgUp>
                            <IconButton color="inherit" onClick={ onSidebarOpen }>
                                <MenuIcon />
                            </IconButton>
                        </Hidden>
                    </>
                )}
            </Toolbar>
        </AppBar>
    )
}

export default connect(null, { logout })(Topbar);

const useStyles = makeStyles(theme => ({
    root: {
        boxShadow: "none"
    },
    flexGrow: {
        flexGrow: 1
    },
    signOutButton: {
        marginLeft: theme.spacing(1)
    }
}));
