import React                from "react";
import { makeStyles }       from "@material-ui/styles";
import { connect }          from "react-redux";
import ApprovedIcon         from "@material-ui/icons/CheckCircle";
import WaitingIcon          from "@material-ui/icons/HourglassEmpty";
import RefusedIcon          from "@material-ui/icons/HighlightOff";

import * as Utils           from "helpers/utils";
import * as C               from "helpers/constants";
import { ProfilePicture }   from "components";
import {
    CardActionArea,
    CardContent,
    CardActions,
    Typography,
    Divider,
    Card,
    Grid,
} from "@material-ui/core";

const HelpCard = ({ className, help, loggedUser }) => {
    const classes = useStyles();
    const goToProfile = () => Utils.openModal("HelpDetails", help);
    const userData = (loggedUser._id === help.user._id) ? help.volunteer : help.user;
    const color = C.HelpColors[help.status];
    const Icon = (C.APPROVED === help.status) ? ApprovedIcon : (C.REFUSED === help.status) ? RefusedIcon : WaitingIcon;

    return (
        <CardActionArea onClick={ goToProfile }>
            <Card className={ classes.root }>
                <CardContent className={ classes.card }>
                    <ProfilePicture baseURL source={ userData.picture } size={ 80 }/>
                    <div className={ classes.description }>
                        <Typography align="left" gutterBottom variant="h4" title={ userData.name }>{ Utils.limitText(userData.name, 22) }</Typography>
                        <Typography align="left" variant="subtitle1" title={ help.description }>{ Utils.limitText(help.description) }</Typography>
                        <Typography align="left" variant="subtitle2">Feito em { Utils.getDate(help.date) } as { Utils.getTime(help.date) }</Typography>
                    </div>
                </CardContent>
                <Divider />
                <CardActions>
                    <Grid container justify="space-between">
                        <Grid item className={ classes.statsItem }>
                            <Icon style={{ color }} className={ classes.statsIcon } />
                            <Typography style={{ color }} display="inline" variant="body2">{ C.HelpTexts[help.status] }</Typography>
                        </Grid>
                    </Grid>
                </CardActions>
            </Card>
        </CardActionArea>
    )
}

const mapStateToProps = state => ({
    loggedUser: state.user.user
});

export default connect(mapStateToProps)(HelpCard);

const useStyles = makeStyles(theme => ({
    root: {},
    card: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        alignItems: "center",
    },
    description: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "flex-start",
        paddingLeft: 15
    },
    image: {
        width: "100%"
    },
    statsItem: {
        display: "flex",
        alignItems: "center"
    },
    statsIcon: {
        color: theme.palette.icon,
        marginRight: theme.spacing(1)
    }
}));
