const NewAccount = ({ history }) => {
    const classes = useStyles();
    const [ formState, setFormState ] = useState({
        isValid: false,
        values: {},
        touched: {},
        errors: {}
    });

    // useEffect(() => {
    //     const errors = validate(formState.values, schema);
    //     setFormState(formState => ({
    //         ...formState,
    //         isValid: errors ? false : true,
    //         errors: errors || {}
    //     }));
    // }, [formState.values]);

    const handleChange = (event, mask) => {
        const { name, type, checked, value: v } = event.target;
        const value = mask ? Utils.mask(v, mask) : v;

        event.persist();

        setFormState(formState => ({
            ...formState,
            values: {
                ...formState.values,
                [name]: type === "checkbox" ? checked : value
            },
            touched: {
                ...formState.touched,
                [name]: true
            }
        }));
    };

    const handleBack = () => history.goBack();

    const handleNewAccount = event => {
        event.preventDefault();
        history.push("/home");
    }

    const hasError = field => formState.touched[field] && formState.errors[field] ? true : false;

    return (
        <div className={classes.root}>
            <Grid className={classes.grid} container>
                <Grid className={classes.quoteContainer} item lg={5}>
                    <div className={classes.quote}>
                        <div className={classes.quoteInner}>
                            <Typography className={classes.quoteText} variant="h1">Dar as mãos por uma boa causa:<br/>esse é o nosso jogo!</Typography>
                            <Typography className={classes.bio} variant="body2">(No sentido figurado, nada de contato físico)</Typography>
                            <Typography className={classes.name} variant="body1">Autor Desconhecido</Typography>
                        </div>
                    </div>
                </Grid>
                <Grid className={classes.content} item lg={7} xs={12}>
                    <div className={classes.content}>
                        <div className={classes.contentHeader}>
                          <IconButton onClick={handleBack}>
                              <ArrowBackIcon />
                          </IconButton>
                        </div>
                        <div className={classes.contentBody}>
                            <form className={classes.form} onSubmit={handleNewAccount}>
                                <Typography className={classes.title} variant="h2">Criar conta</Typography>
                                <Typography color="textSecondary" gutterBottom>Use seu email para criar uma conta</Typography>
                                <Input
                                    field="name"
                                    label="Nome completo"
                                    classes={ classes }
                                    formState={ formState }
                                    onChange={ handleChange }
                                    hasError={ hasError }
                                />
                                <Input
                                    field="cpf"
                                    label="CPF"
                                    mask="###.###.###-##"
                                    classes={ classes }
                                    formState={ formState }
                                    onChange={ handleChange }
                                    hasError={ hasError }
                                />
                                <Input
                                    mask="(##)#####-####"
                                    field="phone"
                                    label="Telefone"
                                    classes={ classes }
                                    formState={ formState }
                                    onChange={ handleChange }
                                    hasError={ hasError }
                                    type="tel"
                                />
                                <Input
                                    mask="##/##/####"
                                    field="birthdate"
                                    label="Data de Nascimento"
                                    classes={ classes }
                                    formState={ formState }
                                    onChange={ handleChange }
                                    hasError={ hasError }
                                    type="tel"
                                />
                                <Input
                                    field="email"
                                    label="Email"
                                    classes={ classes }
                                    formState={ formState }
                                    onChange={ handleChange }
                                    hasError={ hasError }
                                    type="email"
                                />
                                <Input
                                    field="password"
                                    label="Senha"
                                    classes={ classes }
                                    formState={ formState }
                                    onChange={ handleChange }
                                    hasError={ hasError }
                                    type="password"
                                />

                                <TextField
                                    fullWidth
                                    label=""
                                    margin="dense"
                                    name="sex"
                                    onChange={ handleChange }
                                    select
                                    // eslint-disable-next-line react/jsx-sort-props
                                    SelectProps={{ native: true }}
                                    variant="outlined"
                                    className={ classes.select }
                                >
                                    <option value="m">Homem</option>
                                    <option value="f">Mulher</option>
                                </TextField>

                                <div className={classes.policy}>
                                    <Checkbox
                                        checked={formState.values.policy || false}
                                        className={classes.policyCheckbox}
                                        color="primary"
                                        name="policy"
                                        onChange={ handleChange }
                                    />
                                    <Typography className={classes.policyText} color="textSecondary" variant="body1">
                                        {"Eu aceito os "}
                                        <Link
                                            color="primary"
                                            component={RouterLink}
                                            to="#"
                                            underline="always"
                                            variant="h6"
                                        >
                                            Termos e Condições
                                        </Link>
                                    </Typography>
                                </div>
                                {(hasError("policy")) && (
                                    <FormHelperText error>
                                        { formState.errors.policy[0] }
                                    </FormHelperText>
                                )}
                                <Button
                                    className={classes.signUpButton}
                                    color="primary"
                                    disabled={!formState.isValid}
                                    fullWidth
                                    size="large"
                                    type="submit"
                                    variant="contained"
                                >
                                    Criar conta
                                </Button>
                                <Typography color="textSecondary" variant="body1">
                                    {"Já possui conta? "}
                                    <Link component={ RouterLink } to="/login" variant="h6">Faça login</Link>
                                </Typography>
                            </form>
                        </div>
                    </div>
                </Grid>
            </Grid>
        </div>
    )
}

const Input = ({ field, label, classes, formState, onChange, type, hasError, mask }) => (
    <TextField
        className={ classes.textField }
        error={ hasError(field) }
        fullWidth
        helperText={ hasError(field) ? formState.errors[field][0] : null }
        label={ label }
        name={ field }
        onChange={ e => onChange(e, mask) }
        type={ type ? type : "text" }
        value={ formState.values[field] || ""}
        variant="outlined"
    />
)

NewAccount.propTypes = {
    history: PropTypes.object
}

export default withRouter(NewAccount);

