import React, { useState }  from "react";
import { connect }          from "react-redux";
import { makeStyles }       from "@material-ui/styles";
import * as Utils           from "helpers/utils";
import * as C               from "helpers/constants";
import { updateHelp }       from "../actions/help";
import { ProfilePicture }   from "components";
import {
    Typography,
    Button,
    Grid,
    Link,
} from "@material-ui/core";

const HelpDetails = ({ data, getUserInfo, loggedUser, updateHelp }) => {
    const [ help, setHelp ] = useState(data);
    const classes = useStyles();
    const isVolunteer = loggedUser._id === help.volunteer._id;
    const userData = isVolunteer ? help.user : help.volunteer;
    const color = C.HelpColors[help.status];

    const userDetail = () => Utils.openModal("ViewProfile", userData._id);
    const acceptHelp = () => makeRequest(true);
    const refuseHelp = () => makeRequest(false);
    const makeRequest = approved => updateHelp(help._id, approved, status => {
        if (!status) {
            Utils.closeModal();
            return Utils.notificationError(`Não foi possível ${approved ? "aprovar" : "recusar"} o pedido no momento.`);
        }

        setHelp({ ...help, status: C.APPROVED });
        return Utils.notificationSuccess(`Pedido ${approved ? "aprovado" : "recusado"} com sucesso! ${approved ? "Entre em contato com a pessoa." : ""}`);
    });

    return (
        <div className={ classes.paper }>
            <Grid container spacing={ 4 }>
                <Grid item xs={ 4 } md={ 3 }>
                    <ProfilePicture baseURL source={ userData.picture } size={ 90 }/>
                </Grid>

                <Grid item xs={ 8 } md={ 9 } className={ classes.content }>
                    <Link onClick={ userDetail } variant="subtitle1">{ userData.name }</Link>
                    <Typography variant="overline" style={{ margin: "5px 0px" }}>Pedido feito em { Utils.getDate(help.date) } às { Utils.getTime(help.date) }</Typography>
                    <Typography variant="overline" style={{ color, margin: "2px 0px 5px" }}>{ C.HelpTexts[help.status] }</Typography>
                </Grid>

                <Grid item xs={ 12 } className={ classes.content }>
                    <Typography variant="h5">Detalhes do Pedido</Typography>
                    <Typography variant="subtitle2">{ help.description }</Typography>
                </Grid>

                {(C.APPROVED === help.status) && (
                    <Grid item xs={ 12 } className={ classes.content }>
                        <Typography variant="h5">Dados do usuários</Typography>
                        {(userData.phone) && (
                            <Link href={`tel:${userData.phone}`} variant="subtitle2" className={ classes.link }>{ userData.phone }</Link>
                        )}
                        {(userData.email) && (
                            <Link href={`mailto:${userData.email}`} variant="subtitle2" className={ classes.link }>{ userData.email }</Link>
                        )}
                    </Grid>
                )}

                {/*(C.APPROVED === help.status && !isVolunteer) && (
                    <Grid item xs={ 12 } style={{ paddingTop: 0 }}>
                        <Button fullWidth onClick={ reportUser } color="secondary" variant="contained">Denunciar Usuário</Button>
                    </Grid>
                )*/}

                {(C.WAITING_APPROVAL === help.status && isVolunteer) && (
                    <>
                        <Grid item xs={ 12 }>
                            <Button fullWidth onClick={ acceptHelp } color="primary" className={ classes.requestHelp } variant="contained">Aceitar Pedido</Button>
                        </Grid>
                        <Grid item xs={ 12 } style={{ paddingTop: 0 }}>
                            <Button fullWidth onClick={ refuseHelp } color="secondary" variant="contained">Recusar Pedido</Button>
                        </Grid>
                    </>
                )}
            </Grid>
        </div>
    )
}

const mapStateToProps = state => ({
    loggedUser: state.user.user
});

export default connect(mapStateToProps, { updateHelp })(HelpDetails);

const useStyles = makeStyles((theme) => ({
    paper: {
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing(4),
        borderRadius: 10,

        [theme.breakpoints.down("xs")]: {
            maxWidth: "90%",
        },
        [theme.breakpoints.up("sm")]: {
            maxWidth: 650,
        },
        [theme.breakpoints.up("md")]: {
            maxWidth: 650,
        },
        [theme.breakpoints.up("lg")]: {
            maxWidth: 650,
        },
    },
    flexCenter: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        marginTop: -10,
        marginBottom: -15
    },
    content: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "flex-start",
    },
    requestHelp: {
        background: "#149505",
        "&:hover": {
            background: "#107505",
        }
    },
    link: {
        marginTop: 5
    }
}));
