import React, { useState, useEffect } from "react";
import { connect }          from "react-redux";
import { makeStyles }       from "@material-ui/styles";
import { Avatar }           from "@material-ui/core";
import { Badge }            from "components";
import { BASE_URL }         from "helpers/constants";
import placeholder          from "assets/img/profile.png";

const ProfilePicture = ({ user, size, source, baseURL }) => {
    const classes = useStyles();
    const [ error, setError ] = useState(false);
    const styles = { width: size, height: size, borderRadius: size };
    let picture = `${BASE_URL}/${user.picture}`;

    if (source) {
        if (baseURL)
            picture = `${BASE_URL}/`;

        picture = source;
    }

    useEffect(() => setError(false), [source, user]);

    return (
        <div style={{ position: "relative", ...styles }}>
            <Avatar
                onError={() => setError(true)}
                className={ classes.avatar }
                src={ error ? placeholder : picture }
                style={ styles }
            />
            <Badge className={ classes.badge } size={ size*0.3 } shield style={{ bottom: -0, right: 5 }}/>
        </div>
    )
}

const mapStateToProps = state => ({
    user: state.user.user
});

ProfilePicture.defaultProps = {
    size: 100,
    source: null
}

export default connect(mapStateToProps)(ProfilePicture);

const useStyles = makeStyles(theme => ({
    avatar: {
        flexShrink: 0,
        flexGrow: 0,
        zIndex: 1,
        border: "1px solid rgba(0,0,0,0.15)"
    },
    ".avatar img": {

    },
    badge: {
        position: "absolute",
        zIndex: 1
    }
}));
