import React, { useEffect, useState } from "react";
import GoogleMapReact                 from "google-map-react";
import { makeStyles }                 from "@material-ui/styles";
import { usePosition }                from "use-position";
import { connect }                    from "react-redux";
import { getNearbyVolunteers }        from "actions/user";
import * as Utils                     from "helpers/utils";
import pin                            from "assets/img/pin.png";
import { Badge }                      from "components";
import {
    CircularProgress
} from "@material-ui/core";

const useStyles = makeStyles(theme => ({
    root: {
        height: "100%",
        width: "100%",
        display: "flex",
        justifyContent: "center",
        alignItems: "center"
    }
}));

const Home = ({ getNearbyVolunteers }) => {
    const classes = useStyles();
    const [ geometry, setGeometry ] = useState([]);
    const [ users, setUsers ] = useState([]);
    const { latitude, longitude, error } = usePosition();

    const onRegionChange = ({ bounds, ...rest }) => {
        console.clear();

        let points = [];

        for (let prop in bounds)
            if (bounds[prop])
                points.push({ latitude: bounds[prop].lat, longitude: bounds[prop].lng });

        const region = Utils.getRegionWithDelta(points)
        const west = {
            latitude: region.latitude - region.latitudeDelta/2,
            longitude: region.longitude - region.longitudeDelta/2
        }
        const east = {
            latitude: region.latitude + region.latitudeDelta/2,
            longitude: region.longitude + region.longitudeDelta/2
        }

        return getNearbyVolunteers({ west, east }, res => {
            if (res !== false)
                return setUsers(res);

            return Utils.notificationError("Não foi possível listar os voluntários próximos da sua região");
        });
    }

    useEffect(() => {
        if (geometry.length === 0) {
            if (latitude) {
                setGeometry([latitude, longitude]);
            } else {
                setGeometry([-22.1169351, -41.4737450]);
            }
        }
    }, [latitude, longitude, error, geometry]);

    return (
        <div className={ classes.root }>
            {(geometry.length === 0 && !error) && (
                <CircularProgress />
            )}
            {(geometry.length > 0) && (
                <GoogleMapReact
                    bootstrapURLKeys={{ key: "AIzaSyAu5pp3fZ4T9I39r7G6D1OF2_-06exEm9U" }}
                    defaultZoom={ 11 }
                    options={{
                        styles: require("./mapStyle.json"),
                        maxZoom: 14
                    }}
                    defaultCenter={{ lat: geometry[0], lng: geometry[1] }}
                    onChange={ onRegionChange }
                >
                    <img lat={ geometry[0] } lng={ geometry[1] } text="Lucas Craveiro Paes" src={ pin } width="60px" alt="User Position"/>

                    { users.map(user =>
                        <Badge key={ user._id } userID={ user._id } lat={ user.latitude } lng={ user.longitude } text={ user.name }/>
                    )}
                </GoogleMapReact>
            )}
        </div>
    );
};

export default connect(null, { getNearbyVolunteers })(Home);
