import request from "helpers/request";

export function newHelp(volunteerID, description, callback) {
    return dispatch => request({
        url: "/helps",
        method: "POST",
        data: {
            volunteerID,
            description
        }
    }).then(res => {
        return callback(res.status);
    }).catch(err => {
        return callback(false);
    })
}

export function getHelps(filter, callback) {
    return dispatch => request({
        url: "/helps?filter=" + filter,
        method: "GET",
    }).then(res => {
        if (res.status === true)
            return callback(res.helps);

        return callback(res.status);
    }).catch(err => {
        return callback(false);
    })
}

export function updateHelp(helpID, approved, callback) {
    return dispatch => request({
        url: "/helps/" + helpID,
        method: "PUT",
        data: {
            approved
        }
    }).then(res => {
        return callback(res.status);
    }).catch(err => {
        return callback(false);
    })
}

export function newRating(helpID, stars, callback = () => null) {
    let comment = "...";
    return dispatch => request({
        url: "/helps/rating/" + helpID,
        method: "PUT",
        data: {
            stars, comment
        }
    }).then(res => {
        return callback(res.status);
    }).catch(err => {
        return callback(false);
    })
}
