import { sha512 } from "js-sha512";
import request    from "../helpers/request";
import * as c     from "../helpers/constants";

export function login(login, password, callback) {
    return dispatch => request({
        url: "/users/login",
        method: "POST",
        data: {
            login: login,
            password: sha512(password),
            web: true
        }
    }).then(res => {
        if (res.status !== true)
            return callback(false);

        dispatch({ type: c.SET_USER, user: res.user });
        return callback(true, res);
    }).catch(err => {
        return callback(false);
    })
}

export function logout() {
    return dispatch => {
        window.location.href = "/login";
        dispatch({ type: c.LOGOUT });
    }
}

export function newUser(data, callback) {
    return dispatch => request({
        url: "/users",
        method: "POST",
        data: {
            ...data,
            password: sha512(data.password)
        }
    }).then(res => {
        if (res.status !== true)
            return callback(false);

        callback(true, res);
        return dispatch({ type: c.SET_USER, user: res.data });
    }).catch(err => {
        return callback(false);
    })
}

export function updateUser(data, callback = () => null) {
    if (data.password)
        data.password = sha512(data.password);

    return dispatch => request({
        url: "/users",
        method: "PUT",
        data: data
    }).then(res => {
        if (res.status !== true)
            return callback(false);

        callback(true);
        return dispatch({ type: c.SET_USER, user: data });
    }).catch(err => {
        return callback(false);
    })
}

export function updatePicture(picture, callback = () => null) {
    let data = new FormData();
    data.append("picture", picture);

    return dispatch => request({
        url: "/users/picture",
        method: "PUT",
        headers: {
            "Content-Type": "multipart/form-data",
        },
        data: data,
    }).then(res => {
        if (res.status !== true)
            return callback(false);

        callback(true);

        return dispatch({
            type: c.SET_USER,
            user: {
                picture: res.picture
            }
        });
    }).catch(err => {
        return callback(false);
    })
}

export function searchUsers(name, callback) {
    return dispatch => request({
        url: "/users/search?name=" + name,
        method: "GET"
    }).then(res => {
        if (res.status !== true)
            return callback(false);

        return callback(res.users);
    }).catch(err => {
        return callback(false);
    })
}

export function getUserInfo(userID, callback) {
    return dispatch => request({
        url: "/users/" + userID,
        method: "GET"
    }).then(res => {
        if (res.status !== true)
            return callback(false);

        return callback(res.user);
    }).catch(err => {
        return callback(false);
    })
}

export function newReport(userID, description, callback) {
    return dispatch => request({
        url: "/reports/",
        method: "POST",
        data: {
            userID, description
        }
    }).then(res => {
        return callback(res.status);
    }).catch(err => {
        return callback(false);
    })
}

export function getNearbyVolunteers(data, callback) {
    console.log(data);
    return dispatch => request({
        url: "/users/search?geometry=" + encodeURIComponent(JSON.stringify(data)),
        method: "GET"
    }).then(res => {
        return callback(res.users);
    }).catch(err => {
        return callback(false);
    })
}

export function updateLanguage(language) {
    return dispatch => dispatch({ type: c.UPDATE_LANGUAGE, language });
}
