import React                from "react";
import { makeStyles }       from "@material-ui/styles";
import StarsIcon            from "@material-ui/icons/Stars";
import * as Utils           from "helpers/utils";
import { ProfilePicture }   from "components";
import {
    CardActionArea,
    CardContent,
    CardActions,
    Typography,
    Divider,
    Card,
    Grid,
} from "@material-ui/core";

const UserCard = ({ className, user }) => {
    const classes = useStyles();
    const goToProfile = () => Utils.openModal("ViewProfile", user._id);

    return (
        <CardActionArea onClick={ goToProfile }>
            <Card className={ classes.root }>
                <CardContent className={ classes.card }>
                    <ProfilePicture baseURL source={ user.picture } size={ 80 }/>
                    <div className={ classes.description }>
                        <Typography align="left" gutterBottom variant="h4">{ user.name }</Typography>
                        <Typography align="left" variant="body1">{ Utils.getAddress(user.address, true) }</Typography>
                    </div>
                </CardContent>
                <Divider />
                <CardActions>
                    <Grid container justify="space-between">
                        <Grid item className={ classes.statsItem }>
                            <StarsIcon className={ classes.statsIcon } />
                            <Typography display="inline" variant="body2">Voluntário 5 estrelas</Typography>
                        </Grid>
                    </Grid>
                </CardActions>
            </Card>
        </CardActionArea>
    )
}

export default UserCard;

const useStyles = makeStyles(theme => ({
    root: {},
    card: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "flex-start",
        alignItems: "center",
    },
    description: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "flex-start",
        paddingLeft: 15
    },
    image: {
        width: "100%"
    },
    statsItem: {
        display: "flex",
        alignItems: "center"
    },
    statsIcon: {
        color: theme.palette.icon,
        marginRight: theme.spacing(1)
    }
}));
