import React, { useState }  from "react";
import { makeStyles }       from "@material-ui/styles";
import { connect }          from "react-redux";
import { CircleCheckbox }   from "components";
import { updateUser }       from "actions/user";
import * as Utils           from "helpers/utils";
import {
    CardContent,
    CardActions,
    CardHeader,
    Typography,
    TextField,
    Divider,
    Button,
    Card,
    Grid,
    Box
} from "@material-ui/core";

const labels = {
    name: "Nome Completo",
    email: "Email",
    cpf: "CPF",
    birthdate: "Data de Nascimento",
    sex: "Sexo",
    phone: "Telefone",
    password: "Senha",

    street: "Rua",
    number: "Número",
    complement: "Complemento",
    neighborhood: "Bairro",
    city: "Cidade",
    state: "Estado",
    country: "País",
}

const validateMask = (v, f) => {
    if (f === "cpf")
        return Utils.mask(v, "###.###.###-##");

    if (f === "birthdate")
        return Utils.mask(v, "##/##/####");

    if (f === "phone")
        return Utils.mask(v, "(##)#####-####");

    return v;
}

const AccountDetails = ({ className, user, updateUser }) => {
    const classes = useStyles();
    const [ values, setValues ] = useState({
        name: user.name,
        login: user.login,
        email: user.email,
        cpf: user.cpf,
        address: user.address,
        birthdate: user.birthdate,
        sex: user.sex,
        phone: user.phone,
        password: "123456",
    });

    const setSex = sex => () => setValues({ ...values, sex });
    const handleChange = ({ target: { name, value }}) => setValues({ ...values, [name]: validateMask(value, name) });
    const handleChangeOnAddress = ({ target: { name, value }}) => setValues({
        ...values,
        address: {
            ...values.address,
            [name]: value,
        }
    });

    const onButtonPress = () => {
        let data = { ...values };

        if (data.password === "123456")
            delete data.password;

        return updateUser(data, (status, res) => {
            if (status)
                return Utils.notificationSuccess("Dados atualizados com sucesso!");

            return  Utils.notificationError("Não foi possível atualizar os dados no momento");
        });
    }

    return (
        <Card className={ className }>
            <form autoComplete="off" noValidate>
                <CardHeader
                    title="Meu Perfil"
                    subheader="Edite suas informações nos campos abaixo"
                    action={(
                        <Box display={{ xs: "none", lg: "block", md: "block", xl: "none" }}>
                            <Button onClick={ onButtonPress } color="primary" variant="contained" style={{ margin: 8 }}>Salvar Dados</Button>
                        </Box>
                    )}
                />
                <Divider />
                <CardContent>
                    <Typography variant="h6" style={{ marginBottom: 10 }}>Atualizar Informações</Typography>
                    <Grid container spacing={ 3 }>
                        <Grid item md={ 6 } xs={ 12 }>
                            <Input name="name" value={ values.name } onChange={ handleChange }/>
                        </Grid>
                        <Grid item md={ 6 } xs={ 12 }>
                            <Input name="email" value={ values.email } onChange={ handleChange }/>
                        </Grid>
                        <Grid item md={ 6 } xs={ 12 }>
                            <Input name="password" value={ values.password } onChange={ handleChange } type="password"/>
                        </Grid>
                        <Grid item md={ 6 } xs={ 12 }>
                            <Input name="birthdate" value={ values.birthdate } onChange={ handleChange }/>
                        </Grid>
                        <Grid item md={ 6 } xs={ 12 }>
                            <Typography variant="subtitle2">Sexo</Typography>
                            <div className={ classes.row }>
                                <CircleCheckbox label="Homem" onCheck={ setSex("m") } checked={ values.sex === "m" } style={{ marginRight: 20 }}/>
                                <CircleCheckbox label="Mulher" onCheck={ setSex("f") } checked={ values.sex === "f" }/>
                            </div>
                        </Grid>
                        <Grid item md={ 6 } xs={ 12 }>
                            <Input name="phone" value={ values.phone } onChange={ handleChange }/>
                        </Grid>
                    </Grid>
                </CardContent>

                <Divider />

                <CardContent>
                    <Typography variant="h6" style={{ marginBottom: 10 }}>Atualizar Endereço</Typography>
                    <Grid container spacing={ 3 }>
                        <Grid item xs={ 8 }>
                            <Input name="street" value={ values.address.street } onChange={ handleChangeOnAddress }/>
                        </Grid>
                        <Grid item xs={ 4 }>
                            <Input name="number" value={ values.address.number } onChange={ handleChangeOnAddress }/>
                        </Grid>
                        <Grid item md={ 4 } xs={ 12 }>
                            <Input name="complement" value={ values.address.complement } onChange={ handleChangeOnAddress }/>
                        </Grid>
                        <Grid item md={ 4 } xs={ 12 }>
                            <Input name="neighborhood" value={ values.address.neighborhood } onChange={ handleChangeOnAddress }/>
                        </Grid>
                        <Grid item md={ 4 } xs={ 12 }>
                            <Input name="city" value={ values.address.city } onChange={ handleChangeOnAddress }/>
                        </Grid>
                        <Grid item md={ 6 } xs={ 12 }>
                            <Input name="state" value={ values.address.state } onChange={ handleChangeOnAddress }/>
                        </Grid>
                        <Grid item md={ 6 } xs={ 12 }>
                            <Input name="country" value={ values.address.country } onChange={ handleChangeOnAddress }/>
                        </Grid>
                    </Grid>
                </CardContent>


                <Box display={{ xs: "block", lg: "none", md: "none", xl: "block" }}>
                    <CardActions>
                        <Button onClick={ onButtonPress } color="primary" variant="contained">Salvar Dados</Button>
                    </CardActions>
                </Box>
            </form>
        </Card>
    )
};

const mapStateToProps = state => ({
    user: state.user.user
});

export default connect(mapStateToProps, { updateUser })(AccountDetails);

const Input = ({ label, name, onChange, value, ...rest }) => (
    <TextField
        fullWidth
        label={ labels[name] }
        margin="dense"
        name={ name }
        onChange={ onChange }
        required
        value={ value }
        variant="outlined"
        {...rest}
    />
)

const useStyles = makeStyles(() => ({
    row: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center"
    }
}));
