import React, { useState, useEffect }   from "react";
import { makeStyles }                   from "@material-ui/styles";
import { Typography }                   from "@material-ui/core";
import RadioButtonChecked               from "@material-ui/icons/RadioButtonChecked";
import RadioButtonUnchecked             from "@material-ui/icons/RadioButtonUnchecked";

const CircleCheckbox = ({ label, onCheck, checked, style }) => {
    const classes = useStyles();
    const [ check, setCheck ] = useState(false);
    const Icon = check ? RadioButtonChecked : RadioButtonUnchecked;
    const toggle = e => {
        e.preventDefault();
        
        if (typeof checked == "undefined")
            setCheck(!check);

        return onCheck(!check);
    }

    useEffect(() => {
        setCheck(checked);
    }, [checked]);

    return (
        <button onClick={ toggle } className={ classes.row } style={ style }>
            <Icon className={ classes.icon }/>
            <Typography variant="body1" className={ classes.label }>{ label }</Typography>
        </button>
    )
}

export default CircleCheckbox;

const useStyles = makeStyles(theme => ({
    row: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        background: "transparent",
        border: "none"
    },
    label: {
        fontSize: 14,
        marginLeft: 7,
        color: "#444"
    },
    icon: {
        color: "#666"
    }
}));
