import React, { useEffect, useCallback } from "react";
import { makeStyles }           from "@material-ui/styles";
import { connect }              from "react-redux";
import { updateAlert }          from "actions/general";
import { Typography }           from "@material-ui/core";
import Close                    from "@material-ui/icons/Close";

let timer = null;
const colors = {
    error: "#bc1a1a",
    success: "#1ba11f",
    info: "#3331e7",
}

const Alert = ({ alert, updateAlert }) => {
    const classes = useStyles();
    const closeAlert = useCallback(() => {
        updateAlert(false);
        clearTimeout(timer);
    }, [updateAlert]);

    useEffect(() => {
        if (alert.status === true)
            timer = setTimeout(closeAlert, 6000);

        return () => clearTimeout(timer);
    }, [alert, closeAlert]);

    if (!alert.status)
        return null;

    return (
        <div className={ classes.wrapper }>
            <button onClick={ closeAlert } open={ true } className={ classes.container } style={{ backgroundColor: colors[alert.type] }}>
                <Typography variant="body1" className={ classes.message }>{ alert.message }</Typography>

                <Close className={ classes.closeIcon }/>
            </button>
        </div>
    )
}

const mapStateToProps = state => ({
    alert: { ...state.general.alert },
});

export default connect(mapStateToProps, { updateAlert })(Alert);


const useStyles = makeStyles(() => ({
    wrapper: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "center",
        width: "100%",
    },
    container: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "flex-start",
        padding: "10px 20px",
        height: 60,
        width: 400,
        position: "absolute",
        bottom: 20,
        marginLeft: "auto",
        marginRight: "auto",
        border: "none",
        borderRadius: 6,
        background: "transparent",
        zIndex: 99999
    },
    message: {
        color: "#FFF",
        fontSize: 16
    },
    closeIcon: {
        color: "#FFF",
        position: "absolute",
        right: 5,
        top: 5,
        fontSize: 18
    }
}));
