import React, { useState, useEffect } from "react";
import { connect }      from "react-redux";
import { getUserInfo }  from "actions/user";
import { makeStyles }   from "@material-ui/styles";
import Stars            from "simple-rating-stars";
import * as Utils       from "helpers/utils";
import {
    Typography,
    Button,
    Grid,
} from "@material-ui/core";
import {
    ProfilePicture,
    Loader,
} from "components";

const ViewProfile = ({ data, getUserInfo }) => {
    const classes = useStyles();
    const [ user, setUser ] = useState(null);
    const [ loader, setLoader ] = useState(true);
    const requestHelp = () => null;
    const reportUser = () => null;

    useEffect(() => {
        getUserInfo(data, res => {
            setLoader(false);

            if (res !== false)
                return setUser(res);

            Utils.closeModal();
            return Utils.notificationError("Não foi possível exibir os dados do usuário");
        });
    }, [data, getUserInfo]);

    return (
        <div className={ classes.paper }>
            <Loader active={ loader }/>
            {(user) && (
                <Grid container spacing={ 4 }>
                    <Grid item xs={ 4 } md={ 3 }>
                        <ProfilePicture baseURL source={ user.picture } size={ 90 }/>
                    </Grid>
                    <Grid item xs={ 8 } md={ 9 } className={ classes.content }>
                        <Typography variant="subtitle1">{ user.name }</Typography>
                        <Typography variant="caption"><i>{ Utils.calculateAge(user.birthdate) } anos</i></Typography>
                        <Typography variant="subtitle2">{ Utils.getAddress(user.address, true) }</Typography>
                    </Grid>
                    <Grid item xs={ 12 } className={ classes.flexCenter }>
                        <Typography style={{ marginBottom: 8 }} variant="overline">{ user.ratings ? `Esse voluntário possui ${user.ratings} estrelas` : "Esse voluntário não possui avaliações" }</Typography>
                        <Stars
                            stars={ user.ratings ? parseInt(user.ratings) : 0 }
                            outOf={ 5 }
                            full="#F1C40F"
                            empty="#BFC5C8"
                            stroke="transparent"
                        />
                    </Grid>
                    <Grid item xs={ 12 }>
                        <Button fullWidth onClick={ requestHelp } color="primary" className={ classes.requestHelp } variant="contained">Solicitar Ajuda</Button>
                    </Grid>
                    <Grid item xs={ 12 } style={{ paddingTop: 0 }}>
                        <Button fullWidth onClick={ reportUser } color="secondary" variant="contained">Denunciar Usuário</Button>
                    </Grid>
                </Grid>
            )}
        </div>
    )
}

export default connect(null, { getUserInfo })(ViewProfile);

const useStyles = makeStyles((theme) => ({
    paper: {
        backgroundColor: theme.palette.background.paper,
        boxShadow: theme.shadows[5],
        padding: theme.spacing(4),
        borderRadius: 10,
        minWidth: 350,

        [theme.breakpoints.down("xs")]: {
            maxWidth: "90%",
        },
        [theme.breakpoints.up("sm")]: {
            maxWidth: 500,
        },
        [theme.breakpoints.up("md")]: {
            maxWidth: 500,
        },
        [theme.breakpoints.up("lg")]: {
            maxWidth: 450,
        },
    },
    flexCenter: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "center",
        marginTop: -10,
        marginBottom: -15
    },
    content: {
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        alignItems: "flex-start",
    },
    requestHelp: {
        background: "#149505",
        "&:hover": {
            background: "#107505",
        }
    }
}));
