import React            from "react";
import { makeStyles }   from "@material-ui/styles";
import * as Utils       from "helpers/utils";
import {
    CardContent,
    Typography,
    Card,
    Grid,
    Link as UILink,
} from "@material-ui/core";

const About = () => {
    const classes = useStyles();
    const onDev = "(Ainda em desenvolvimento)";

    return (
        <div className={ classes.root }>
            <Grid container spacing={ 4 }>
                <Grid item md={ 7 } xs={ 12 }>
                    <Card>
                        <CardContent>
                            <Text justify>Esse projeto foi desenvolvido por um grupo de amigos, que gostariam de aproximar voluntários e as pessoas mais necessitadas nesse momento crítico que estamos vivendo atualmente.</Text>
                            <Text justify>Abaixo temos as referências de todos os integrantes, com links de contato. A nossa equipe é formada por:</Text>

                            <Link href={ Utils.handleLink("https://lucascraveiropaes.github.io/lucascraveiropaes/") }>Lucas Craveiro Paes</Link>
                            <SubText>Idealizador e desenvolvedor responsável pelo Aplicativo e API</SubText>
                            <br/>

                            <Link href={ Utils.handleLink("https://www.linkedin.com/in/beatrizdarochapereira") }>Beatriz da Rocha Pereira</Link>
                            <SubText>Idealizadora e desenvolvedora da versão web</SubText>
                            <Italic>{ onDev }</Italic>
                            <br/>

                            <Link href={ Utils.handleLink("https://www.linkedin.com/in/victorlimams/") }>Victor Lima</Link>
                            <SubText>Co-desenvolvedor da API</SubText>
                            <br/>

                            <Link href={ Utils.handleLink("luizcarloseliasdg@gmail.com") }>Luiz Carlos Elias</Link>
                            <SubText>Designer responsável pela interface e identidade visual</SubText>
                            <br/>

                            <Link href={ Utils.handleLink("https://www.linkedin.com/in/jessicacastros/") }>Jéssica Casto</Link>
                            <SubText>Desenvolvedora da versão web</SubText>
                            <Italic>{ onDev }</Italic>
                            <br/>

                            <Link href={ Utils.handleLink("https://www.linkedin.com/in/jakson-guilherme-alves-da-silva-459ab0161/") }>Jakson Guilherme</Link>
                            <SubText>Desenvolvedor da versão web</SubText>
                            <Italic>{ onDev }</Italic>
                            <br/>

                            <Link href={ Utils.handleLink("https://www.behance.net/thaliamoniza") }>Thalia Moniza</Link>
                            <SubText>Artista e Designer</SubText>
                            <br/>

                            <Link href={ Utils.handleLink("milygama@gmail.com") }>Milene Falcão Lopes Gama</Link>
                            <SubText>Consultora Legal</SubText>
                        </CardContent>
                    </Card>
                </Grid>
            </Grid>
        </div>
    )
}

export default About;

const Text = ({ children, classes }) => <Typography variant="subtitle2">{ children }</Typography>
const Link = ({ children, classes, ...rest }) => <UILink { ...rest } target="_blank" variant="subtitle2">{ children }</UILink>
const SubText = ({ children, classes }) => <Typography variant="body2">{ children }</Typography>
const Italic = ({ children, classes }) => <Typography variant="caption">{ children }</Typography>

const useStyles = makeStyles(theme => ({
    root: {
        padding: theme.spacing(4),

        "& h6": {
            fontSize: 19,
            lineHeight: "24px",
            marginBottom: 10,
        },

        "& a": {
            fontSize: 20,
            lineHeight: "24px",
        },

        "& p": {
            fontSize: 16,
            lineHeight: "24px",
        },

        "& span": {
            display: "block",
            fontSize: 12,
            lineHeight: "18px",
        },
    },
}));
